	// SET HIU NG CHO SLIDER
	.swiper-container {
		.swiper-slide {
			figure {
				img {
					transition      : 1s all ease-in-out;
					transition-delay: 1s;
				}

				// figcaption {
				// 	.title {
				// 		transform       : translateX(-200%);
				// 		transition      : 1s all ease-in-out;
				// 		transition-delay: 1s;
				// 	}

				// 	.description {
				// 		transform       : translateX(-200%);
				// 		transition      : 1s all ease-in-out;
				// 		transition-delay: 2s;
				// 	}
				// }
			}

			&.swiper-slide-active {
				figure {
					img {
						transform: scale(1.1);
					}

					// figcaption {

					// 	.title,
					// 	.description {
					// 		transform: translateX(0);
					// 	}

					// }
				}
			}
		}

		.swiper-button-next {
			background-image   : url(../img/icons/banner-slider-next.svg);
			background-size    : 30px;
			background         : rgba(255, 255, 255, 0.3);
			background-position: 5px;
			background-repeat  : no-repeat;

			right        : -36px;
			width        : 73px;
			height       : 73px;
			z-index      : 1;
			border-radius: 50%;
			transition   : .3s all ease-in-out;

			@media (min-width: 1025px) {
				&:hover {
					border-radius: 50% 0 0 50%;
					right        : -20px;
				}
			}
		}

		.swiper-button-prev {
			background-image   : url(../img/icons/banner-slider-prev.svg);
			background-size    : 30px;
			background         : rgba(255, 255, 255, 0.3);
			background-position: 35px;
			background-repeat  : no-repeat;

			left         : -36px;
			width        : 73px;
			height       : 73px;
			z-index      : 1;
			border-radius: 50%;
			transition   : .3s all ease-in-out;

			@media (min-width: 1025px) {
				&:hover {
					border-radius: 0 50% 50% 0;
					left         : -20px;
				}
			}
		}
	}

	@media (max-width: 1024.98px) {
		.slider-HomeBanner {
			figure {
				position: relative;
				height  : auto;

